<template>
  <div>
    <div class="w-full md:w-2/3 mx-2 md:mx-auto" v-if="user">
      <div class="bg-gray-50 rounded p-3 border-2 border-gray-800 text-center">
        <h2 class="text-lg font-semibold">{{ user.name }}</h2>
        <p>{{ user.phone }}</p>
        <p>{{ user.email }}</p>
      </div>
    </div>

    <div class="md:w-3/4 mx-auto">
      <div class="mt-4 text-center flex flex-wrap justify-center gap-3">
        <button
          :class="`p-3 rounded-lg  font-semibold shadow-sm hover:shadow-lg text-white capitalize ${
            '' == s ? 'bg-black shadow-lg' : 'bg-green-500 shadow-sm'
          }`"
          @click="filterByCluster('')"
        >
          All
        </button>
        <button
          v-for="c in clusters"
          :key="c"
          :class="`p-3 rounded-lg  font-semibold shadow-sm hover:shadow-lg text-white capitalize ${
            c == s ? 'bg-black shadow-lg' : 'bg-green-500 shadow-sm'
          }`"
          @click="filterByCluster(c)"
        >
          {{ c }}
        </button>
      </div>
      <div
        class="
          p-5
          bg-clip-padding
          backdrop-filter backdrop-blur-xl
          bg-opacity-30
          border border-gray-200
          bg-gray-200
          mx-3
          my-5
          rounded-lg
        "
        v-for="(e, i) in exams"
        :key="i"
      >
        <div class="text-center">
          <div>
            <h2
              class="ma-auto balooda text-2xl font-semibold text-gray-700 py-3"
            >
              {{ e.topic }}
            </h2>
            <h2
              class="
                ma-auto
                text-xl
                capitalize
                font-semibold
                text-gray-700
                py-3
              "
            >
              {{ e.cluster }}
            </h2>
          </div>
          <div class="text-lg">
            <h2 class="mt-2 cursive pt-2 text-white">
              Per Question marks: 10 <br />
              Per Question Time: 3 minutes
            </h2>
          </div>

          <div class="mx-auto mt-5 space-y-2">
            <router-link to="/login" class="primary_btn my-2" v-if="!user">
              Start Exam
            </router-link>
            <button
              class="primary_btn my-2"
              v-else-if="!e.purchased"
              @click="purchasealert"
            >
              Start Exam
            </button>

            <router-link
              class="primary_btn my-2"
              v-else-if="e.start < Date.now()"
              :to="`/exam/${e.id}`"
              >Start Exam</router-link
            >
            <button class="primary_btn my-2" v-else @click="upcomingalert">
              Start Exam
            </button>
            <router-link class="mx-2 primary_btn my-2" :to="`/ranking/${e.id}`"
              >Leaderboard</router-link
            >
            <router-link to="/login" class="primary_btn my-2" v-if="!user">
              Solve Sheet
            </router-link>
            <button
              class="primary_btn my-2"
              v-else-if="e.purchased"
              :disabled="e.loading"
              @click="viewSolution(e.exam, e.solve_sheet, e.id)"
            >
              {{ e.loading ? "Loading..." : "Solve Sheet" }}
            </button>
            <button
              class="primary_btn my-2"
              v-else-if="!e.purchased"
              @click="endalert"
            >
              Solve Sheet
            </button>
            <button class="primary_btn my-2" v-else @click="upcomingalert">
              Solve Sheet
            </button>
          </div>
        </div>
      </div>
    </div>
    <div v-if="exams.length < 1" class="mt-10 text-center">
      <loader />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      exams: [],
      clusters: [],
      s: "",
    };
  },
  methods: {
    viewSolution(data, sol, id) {
      this.exams[parseInt(id) - 1].loading = true;
      fetch(data + "?type=check&&phone=" + this.user.email)
        .then((res) => res.json())
        .then((data) => {
          if (data.message == "exists") {
            window.open(sol, "_blank");
            this.exams[parseInt(id) - 1].loading = false;
          } else {
            this.$swal({
              icon: "warning",
              text: "You must give exam to view solution",
            });
            this.exams[parseInt(id) - 1].loading = false;
          }
        });
    },
    getRoutineInfo() {
      let email = localStorage.getItem("acuser");

      const intr = setInterval(() => {
        if (this.user && this.allexams == undefined && email) {
          this.getClasses();
        } else {
          clearInterval(intr);
        }
      }, 2000);
      if (!email) {
        this.getClasses();
      }
    },
    getClasses() {
      fetch(
        "https://script.google.com/macros/s/AKfycbwUTXAm1FHw4j_OiQmoCkVduDbWo6Ewkfcp6sWbfQ07PXtia_ryexQnK8N0H_EoVyE7/exec"
      )
        .then((res) => res.json())
        .then((data) => {
          this.allexams = data.exams.map((r) => ({
            id: r.id.toString(),
            topic: r.topic,
            question: r.question,
            start: new Date(r.end).getTime(),
            solve_sheet: r.solve_sheet,
            loading: false,
            purchased:
              this.user &&
              this.user.clusters.indexOf(r.cluster.replace(" ", "_")) > -1
                ? true
                : false,
            cluster: r.cluster,
            exam: r.question,
          }));
          this.clusters = new Set([...data.exams.map((r) => r.cluster)]);
          this.exams = this.allexams;
        });
    },
    filterByCluster(s) {
      this.s = s;
      this.exams = this.allexams.filter((c) => c.cluster.includes(s));
    },
    endalert() {
      this.$swal({
        icon: "warning",
        title: "You must purchase this cluster",
      });
    },
    upcomingalert() {
      this.$swal({
        icon: "warning",
        title: "Comming soon...",
      });
    },
    purchasealert() {
      this.$swal({
        icon: "warning",
        title: "Not Purchased",
        text: "You must purchase this cluster.",
      });
    },
  },
  created() {
    this.getRoutineInfo();
  },
  computed: {
    user() {
      return this.$store.getters.getUser;
    },
  },
};
</script>

<style lang="scss" scoped>
</style>